@charset "UTF-8";
h1,
h2,
h3,
h4 {
  margin-bottom: 1.25rem;
}

p,
blockquote {
  font-size: 0.8125rem;
  line-height: 1.6;
  font-weight: 600;
}

a,
a:visited,
a:active {
  text-decoration: none;
  background-color: transparent;
}

ul {
  list-style-type: none;
}

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  text-align: left;
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --selection-bg: 277 100% 50%;
  --violet: 263 55% 52%;
  --dark-grayish-blue: 217 19% 35%;
  --dark-blackish-blue: 219 29% 14%;
  --light-gray: 0 0% 81%;
  --Light-grayish-blue: 210 46% 95%;
  --white: 0 0% 100%;
  --green: 153 46% 49%;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    transition-duration: 0.01ms !important;
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

::selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--dark-blackish-blue));
}

::-moz-selection {
  background-color: hsl(var(--selection-bg));
  color: hsl(var(--dark-blackish-blue));
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: "Barlow Semi Condensed", sans-serif;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  max-height: 100%;
  min-height: 100vh;
  background-color: hsl(var(--Light-grayish-blue));
}

.btn {
  display: inline-block;
  border: none;
  margin: 0;
  padding: 1rem 2rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.btn:hover,
.btn:focus {
  background: #0053ba;
}

.btn:focus {
  outline: none;
  outline-offset: -4px;
}

.btn:active {
  transform: scale(0.99);
}

.grid {
  padding: 0.9375rem;
  max-width: 68.75rem;
  color: hsl(var(--white));
}
@media (min-width: 20em) {
  .grid {
    display: flex;
    flex-direction: column;
    gap: 1.5625rem;
    padding: 0.625rem;
  }
}
@media (min-width: 42.5em) {
  .grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5625rem;
    padding: 0.625rem;
  }
}
@media (min-width: 68.75em) {
  .grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5625rem;
    padding: 0.625rem;
  }
}

.grid--item {
  padding: 1.875rem;
  border-radius: 0.5rem;
  box-shadow: hsla(0deg, 0%, 0%, 0.24) 0px 3px 8px;
}
.grid--item:hover {
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.grid__item--1 {
  position: relative;
  grid-column: 1/3;
  background-color: hsl(var(--violet));
}
.grid__item--1::before {
  content: "â€œ";
  position: absolute;
  top: -7.1875rem;
  right: 3.125rem;
  font-size: 37.5rem;
  font-weight: lighter;
  font-family: Arial, Helvetica, sans-serif;
  color: hsl(var(--white)/0.3);
}
.grid__item--1 .grid--item--img {
  border-color: hsl(var(--light-gray));
}

.grid__item--2 {
  background-color: hsl(var(--dark-grayish-blue));
}

.grid__item--3 {
  background-color: hsl(var(--white));
  color: hsl(var(--dark-grayish-blue));
}
.grid__item--3 .grid--item--status {
  color: hsl(var(--dark-grayish-blue)/0.5);
}
.grid__item--3 .grid--item--quote {
  color: hsl(var(--dark-grayish-blue)/0.7);
}

.grid__item--4 {
  grid-column: 2/4;
  background-color: hsl(var(--dark-blackish-blue));
}
.grid__item--4 .grid--item--img {
  border-color: hsl(var(--violet));
}

.grid__item--5 {
  background-color: hsl(var(--white));
  color: hsl(var(--dark-grayish-blue));
}
@media (min-width: 42.5em) {
  .grid__item--5 {
    grid-column: 1/4;
  }
}
@media (min-width: 68.75em) {
  .grid__item--5 {
    grid-column: 4/5;
    grid-row: 1/3;
  }
}
.grid__item--5 .grid--item--status {
  color: hsl(var(--dark-grayish-blue)/0.5);
}
.grid__item--5 .grid--item--quote {
  color: hsl(var(--dark-grayish-blue)/0.7);
}

.grid--item--figure {
  display: flex;
  margin-bottom: 1.25rem;
  align-items: center;
}

.grid--item--img {
  border: 2px solid;
  border-radius: 50%;
  height: 2.5rem;
  width: 2.5rem;
}

.grid--item--figcaption {
  margin-left: 0.625rem;
}

.grid--item--name {
  font-size: 1rem;
}

.grid--item--status {
  color: hsl(var(--white)/0.7);
}

.grid--item--title {
  font-weight: 700;
  margin-bottom: 1.25rem;
  text-shadow: 0 0 #000, 0 5.2em #1095c1;
  overflow: hidden;
  transition: 0.3s;
}
@media (min-width: 42.5em) {
  .grid--item--title {
    font-size: 1.1875rem;
  }
}

.grid--item--quote {
  color: hsl(var(--white)/0.5);
}

.hover-1 {
  color: rgba(0, 0, 0, 0);
  text-shadow: 0 0 hsl(var(--white)), 0 5.2em hsl(var(--white));
  overflow: hidden;
  transition: 0.3s;
}

.hover-1:hover,
.hover-1:focus {
  text-shadow: 0 -5.2em #000, 0 0 hsl(var(--green));
  color: rgba(0, 0, 0, 0);
}

.hover-2 {
  color: rgba(0, 0, 0, 0);
  text-shadow: 0 0 hsl(var(--dark-blackish-blue)), 0 5.2em #1095c1;
  overflow: hidden;
  transition: 0.3s;
}

.hover-2:hover {
  text-shadow: 0 -5.2em #000, 0 0 hsl(var(--green));
}